.game-container {
  display: flex;
  min-height: 100vh;
  margin: auto;
  align-items: center;
  justify-content: center;
}

body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  align-items: center;
  min-height: 30vh;
}

.game-board {
  justify-items: center;
}

.game-info {
  margin-left: 20px;
}

.game-info li {
  padding-bottom: 5px;
}
